// Button.Styled.js
import styled, { css } from 'styled-components';
import theme from '../theme';

export const StyledButtonComponent = styled.button`
  height: 38px;
  width: max-content;
  padding: 11px 28px;
  cursor: pointer;
  border: 2px solid;
  border-radius: 4px;
  font-size: 14px;
  margin-right: 10px;
  margin-top: 10px;
  font-family: Arial;
  font-weight: 600;
  text-transform: uppercase;

  ${({ type, colorEffect, outline, hovers }) => {
    const bgColor = colorEffect ? 'transparent' : theme?.[type];
    const textColor = colorEffect ? theme?.[type] : 'white';
    const borderColor = colorEffect ? theme?.[type] : 'white';

    const dynamicStyles = css`
      background-color: ${bgColor};
      color: ${textColor};
      border: ${borderColor};

      /* Hover effect for outline buttons */
      ${outline &&
      hovers &&
      css`
        &:hover,
        &:active {
          background-color: ${theme?.[type]};
          color: white;
        }
      `}
    `;

    if (colorEffect) {
      return css`
        ${dynamicStyles}
        &:hover,
        &:active {
          background-color: ${theme?.[type]};
          color: white;
        }
      `;
    }

    return dynamicStyles;
  }}
`;
